import React from 'react';

const Card = ({name, id, email}) => {
    // const {name, id, email} = props;
    return (
        <div className='tc bg-light-green dib br3 pa3 na2 grow bw2 shadow-5'>
            <img alt="robots" src={`https://robohash.org/${id}`}/>
            <div>
                <h2>{name}</h2>
                <p>{email}</p>
            </div>
        </div>
    );
};

export default Card;
